<template>
  <div style="width: 100%;">
    <skeleton :nums="4" :iheight="'.4rem'" v-if="isSkeletonLoading"/>
    <div class="container" v-else>
      <img class="top-bg" :src="bannerImg"/>
      <div class="my-rank" v-if="myRank">
        我的带货排名：全国第{{myRank}}名
      </div>
      <div class="list-container">
        <div class="list-title">
          <div class="list-title-text column-one">
            排行
          </div>
          <div class="list-title-text column-two">
