          </div>
        </div>
        <!-- <van-list
          :immediate-check="false"
          :finished="finished"
          v-model="loading"
          van-clearfix
          @load="onLoad"> -->
        <div v-for="(item, index) in rankList" :key="index" class="rank-item">
          <div class="column-one">
            <img
              v-if="index < 3"
              class="icon-rank"
              :src="`http://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/partnerRank/icon_partner_${index}.png`"/>
            <div v-else class="icon-rank text-rank">
              {{index + 1}}
            </div>
          </div>
          <div class="rank-item-name-row column-two">
            <div class="name">
              {{item.name}}
            </div>
            <div class="mobile">
              {{item.mobile}}
            </div>
          </div>
          <div class="mony-text column-two">
            {{item.rewardAmount}}
          </div>
          <div class="sale-num column-four">
            {{item.saleCount}}
          </div>
        </div>
        <!-- </van-list> -->
      </div>
      <div class="rule-text" v-if="ruleText">
        <div style="text-align: center">活动规则</div>
        <p v-html="ruleText"></p>
      </div>
    </div>
  </div>
</template>
<script>
import Skeleton from '@/components/skeleton';
import { getRankList } from '@/http/service/partnerRank';

// const localData = {
//   pageIndex: 1,
//   pageSize: 10,
// };
/**
 * 合伙人排行榜
 */
export default {
  components: {
    Skeleton,
  },
  data() {
    return {
      isSkeletonLoading: true,
      myRank: '',
      rankList: [],
      finished: false,
      loading: true,
      configuration: '',
      bannerImg: '',
      ruleText: '',
    }
  },
  mounted() {
    this.configuration = this.$route.query.configuration
    this.getRankList();
  },
  methods: {
    // onLoad() {
    //   localData.pageIndex++;
    //   this.getRankList();
    // },
    getRankList() {
      getRankList({
        configuration: this.configuration
      }).then(({ data }) => {
        console.log('排行榜:', data);
        this.isSkeletonLoading = false;
        if (data.success) {
          this.rankList = data.data.topList;
          this.bannerImg = data.data.headImg
          this.ruleText = data.data.bottomText
          this.myRank = data.data.ranking;
        }
      });
    }
  }
}
</script>
